         loadBidChartData(): void {
                              
                         
                         var firstDay;
                         var lastDay;

                         if (this.selected_date.length == 0 ){

                              let date = new Date();
                              let year = date.getFullYear(); 
                              let month = date.getMonth();
                                firstDay = new Date(year, month, 1);
                                 lastDay = new Date(year, month + 1, 0);                   
                        }
                        else{

                              firstDay = this.selected_date[0]
                              lastDay = this.selected_date[1]

                        }

                        firstDay = this.datepipe.transform(firstDay,"dd-MMM-y");
                         lastDay = this.datepipe.transform(lastDay,"dd-MMM-y"); 

                              this.positives =[]
                              this.negatives =[]
                              this.tmales = []
                              this.tfemales = []

                               for (var i =0; i < this.DataObjects.length; i++)
                               {                                                                                                                 
                                      this.positives.push(this.DataObjects[i].positive)
                                      this.negatives.push(this.DataObjects[i].negative) 
                                      this.tmales.push(this.DataObjects[i].M)
                                      this.tfemales.push(this.DataObjects[i].F)                            
                              }


                                                

                              this.chartOptions.series= [{
                                                            type: undefined,
                                                            name: 'Positive',
                                                            data: this.positives,
                                                     dataLabels: { enabled: true,inside: false  }
                                                         },
                                                         {
                                                            type: undefined,
                                                            name: 'Negative',
                                                            data: this.negatives,
                                                      dataLabels: { enabled: true, inside: false }

                                                        }];

                               this.chartOptions.subtitle = {
                                                                text: firstDay+' - '+lastDay
                                                            };

                               this.chartOptions1.series= [{

                                                               type: undefined,
                                                               name: 'Male',
                                                               data: this.tmales

                                                           }, {
                                                               type: undefined,
                                                               name: 'Female',
                                                               data: this.tfemales
                                                         }];

                                this.chartOptions1.subtitle = {
                                                                text: firstDay+' - '+lastDay
                                                            };

                                  this.updateFlag = true;
                            
                   }

  
    GenerateData(){

               this.selected_date =[]
               this.page_count= 0;
               this.ItemsCount = 0;

              this.DataObjects.map(function(item){ 

                  for (let value in item) {
                                                
                        if (value != "module")
                        {
                            let vals = item[value];                             
                            if (Number(vals)){  item[value] = 0  }
                        }
   
                     }

               })


              this.newEntrantsObject.map(function(item){ 

                  for (let value in item) {
                                                
                        if (value != "Population_Type")
                        {
                            let vals = item[value];                             
                            if (Number(vals)){  item[value] = 0  }
                        }
   
                     }

               })


              this.ScreenedTotal.map(function(item){ 

                  for (let value in item) {
                                                
                        if (value != "Population_Type")
                        {
                            let vals = item[value];                             
                            if (Number(vals)){  item[value] = 0  }
                        }
   
                     }

               })

              this.TotalsObject.map(function(item){ 

                  for (let value in item) {
                                                
                        if (value != "Population_Type")
                        {
                            let vals = item[value];                             
                            if (Number(vals)){  item[value] = 0  }
                        }
   
                     }

               })


              this.exitedObject.map(function(item){ 

                  for (let value in item) {
                                                
                        if (value != "Population_Type")
                        {
                            let vals = item[value];                             
                            if (Number(vals)){  item[value] = 0  }
                        }
   
                     }

               })

              this.PopulationObject.map(function(item){ 

                  for (let value in item) {
                                                
                        if (value != "Population_Type")
                        {
                            let vals = item[value];                             
                            if (Number(vals)){  item[value] = 0  }
                        }
   
                     }

               })



                let start =this.myReports.get('start').value;
                let end =this.myReports.get('end').value;

                let startsmonth = this.datepipe.transform(start,"MMM-y");
                let startsyear = this.datepipe.transform(start,"y");

                let endsmonth = this.datepipe.transform(end,"MMM-y");
                let endsyear = this.datepipe.transform(end,"y");

                let statdate = this.datepipe.transform(start,"y-MM-d");
                let enddate = this.datepipe.transform(end,"y-MM-d");              

                this.selected_date.push(statdate)
                this.selected_date.push(enddate)

                /*const oneDay = 24 * 60 * 60 * 1000; // hours*minutes*seconds*milliseconds
                const diffDays = Math.round(Math.abs((start - end) / oneDay));

                  this.month = startsmonth+" to "+endsmonth
                  this.year = startsyear+" to "+endsyear

                if (diffDays > 31){
                   
                   this.report_type ="QUARTELY"  
                   this.report_name ="quartely_report"               
                }
                else{
                    
                  this.report_type ="MONTHLY"
                  this.report_name ="monthly_report" 
                }*/

              this.getLoader()  

        }


